import React, { useState } from 'react';
import { Button, Box, CircularProgress, TextField } from '@mui/material';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet'
import { createControlComponent } from "@react-leaflet/core";
import Leaflet from 'leaflet';
import 'leaflet/dist/leaflet.css';
import L from 'leaflet';
import "leaflet-routing-machine";
import AddLocationIcon from '@mui/icons-material/AddLocation';
import { GetServiceProviders } from './Helper';
import Modal from '@mui/material/Modal';
delete Leaflet.Icon.Default.prototype._getIconUrl;
Leaflet.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
});
const marker = new L.Icon({
  iconUrl: require("../Assets/marker-pin.png"),
  iconSize: 40
});
const createRoutineMachineLayer = (props) => {
  const instance = L.Routing.control({
    waypoints: [
      L.latLng(props.source.lat, props.source.lng),
      L.latLng(props.destination.lat, props.destination.lng)
    ],
    collapsible:true,
    lineOptions: {
      styles: [{ color: "#69A628", weight: 4 }]
    },
    show: true,
    addWaypoints: false,
    routeWhileDragging: false,
    //draggableWaypoints: true,
    fitSelectedRoutes: false,
    showAlternatives: false
  });

  return instance;
};

const Navigation = createControlComponent(createRoutineMachineLayer);
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  textAlign: 'center',
  borderRadius: '10px',
  p: 4,
};
function ServiceProvider() {
  const [currentLocation, setClocation] = useState({
    lat: '',
    lon: '',
    preferred: ''
  });
  const [serviceProviders, setServiceProviders] = useState();
  const [toggle, setToggle] = useState(false);
  const [toggleMap, setToggleMap] = useState(false);
  const getServiceProviders = (e) => {
    e.preventDefault();
    setToggle(true)
    setToggleMap(false)
    GetServiceProviders(currentLocation).then((data) => {
      console.log(data);
      setToggle(false);
      if (data.success) {
        if (data.data.status === "OK") {
          setServiceProviders(data.data.results)
          setToggleMap(true)
        } else {
          alert("No service providers arround you")
        }
      } else {
        alert("Something went wrong")
      }
    }).catch((err) => console.log(err))
  }
  const handleChange = (name,event) =>{
    event.preventDefault();
    setClocation({...currentLocation,[name]:event.target.value})
  }
  return (
    <div className="container p-5">
      <Modal
        open={toggle}
        onClose={() => setToggle(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <CircularProgress color="success" />
          <h4 className="test-secondary">Fetching Service Providers</h4>
        </Box>
      </Modal>
      <div className='text-center p-3'>
        <h3 className="msg">Find Nearby Service Provider</h3>
        <TextField id="Lat" sx={{
          width: 500,
          maxWidth: '100%',
        }} label="Latitude" variant="outlined" onChange={e => handleChange("lat",e)}/><br /><br />
        <TextField id="Lon" sx={{
          width: 500,
          maxWidth: '100%',
        }} label="Longitute" variant="outlined" value={currentLocation.lon} onChange={e => handleChange("lon",e)}/><br /><br />
        <TextField id="Pref" sx={{
          width: 500,
          maxWidth: '100%',
        }} label="Preffred Service Provider" variant="outlined" value={currentLocation.preferred} onChange={e => handleChange("preferred",e)}/><br /><br />
        <Button variant="contained" sx={{
          width: 500,
          maxWidth: '100%',
        }} onClick={getServiceProviders} endIcon={<AddLocationIcon />}>Get Nearby Service Providers</Button>
      </div>
      <Box className="pt-3" display={toggleMap? 'block' : 'none'}>
        {currentLocation !== undefined ?
          <MapContainer center={[currentLocation?.lat, currentLocation?.lon]} zoom={13} scrollWheelZoom={true}>
            <TileLayer
              attribution=''
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            <Marker position={[currentLocation?.lat, currentLocation?.lon]}>
              <Popup>
                Here you are
              </Popup>
            </Marker>
            {serviceProviders !== undefined ?
              serviceProviders.map((serviceProvider, index) => {
                if (index < 5) {
                  return (
                    <Marker icon={marker} position={[serviceProvider?.geometry?.location?.lat, serviceProvider?.geometry?.location?.lng]} key={index}>
                      <Popup>
                        {serviceProvider.name}
                      </Popup>
                    </Marker>
                  )
                }
              })
              : ''}
               {serviceProviders !== undefined ?
                <Navigation source={{lat:currentLocation.lat,lng:currentLocation.lon}} destination={{lat:serviceProviders[0]?.geometry?.location?.lat,lng:serviceProviders[0]?.geometry?.location?.lng}}/>
              : ''}
              </MapContainer>
          : ''}
      </Box>
    </div>
  );
}
export default ServiceProvider