import React, { useState, useRef, useEffect } from 'react';
import { Button, Box, CircularProgress, TextField } from '@mui/material';
import "leaflet-routing-machine";
import AddLocationIcon from '@mui/icons-material/AddLocation';
import { GetServiceProviders, GetToken } from './Helper';
import Modal from '@mui/material/Modal';
import {
    useJsApiLoader,
    GoogleMap,
    Marker,
    DirectionsRenderer,
    InfoWindowF,
} from '@react-google-maps/api'
const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    textAlign: 'center',
    borderRadius: '10px',
    p: 4,
};
const getNearestServiceProvider = (source, serviceProviders) => {
    const calculateDistance = (lat1, lng1, lat2, lng2, unit = "N") => {
        var radlat1 = Math.PI * lat1 / 180
        var radlat2 = Math.PI * lat2 / 180
        var theta = lng1 - lng2
        var radtheta = Math.PI * theta / 180
        var dist = Math.sin(radlat1) * Math.sin(radlat2) + Math.cos(radlat1) * Math.cos(radlat2) * Math.cos(radtheta);
        if (dist > 1) {
            dist = 1;
        }
        dist = Math.acos(dist)
        dist = dist * 180 / Math.PI
        dist = dist * 60 * 1.1515
        if (unit === "K") { dist = dist * 1.609344 }
        if (unit === "N") { dist = dist * 0.8684 }
        return {
            distance: dist,
            lat: Number(lat2),
            lng: Number(lng2)

        }
    }
    return new Promise((resolve, reject) => {
        if (serviceProviders !== undefined) {
            var min = calculateDistance(source.lat, source.lng, serviceProviders[0]?.geometry?.location?.lat, serviceProviders[0]?.geometry?.location?.lng)
            resolve(
                serviceProviders.map((serviceProvider, index) => {
                    var result = calculateDistance(source.lat, source.lng, serviceProvider?.geometry?.location?.lat, serviceProvider?.geometry?.location?.lng)
                    if (result.distance < min.distance) {
                        min = result
                    }
                    if (index === serviceProviders.length - 1) {
                        return min;
                    }
                })[serviceProviders.length - 1]
            )
        }
    })
}
function GoogleMaps() {
    const [libraries] = useState(['places']);
    const { isLoaded, loadError } = useJsApiLoader({
        googleMapsApiKey: process.env.REACT_APP_GOOGLE_MAPS_API_KEY,
        libraries: libraries,
    })
    const [map, setMap] = useState(/** @type google.maps.Map */(null))
    const [directionsResponse, setDirectionsResponse] = useState(null)
    const [currentLocation, setClocation] = useState({
        lat: 0,
        lng: 0,
        preferred: ''
    });
    const [serviceProviders, setServiceProviders] = useState();
    const [toggle, setToggle] = useState(false);
    const [toggleMap, setToggleMap] = useState(false);
    const [drawPreffred, setDrawPreffred] = useState(false)
    // eslint-disable-next-line no-undef
    var iconMarker ;
    useEffect(()=>{
        if(isLoaded){
            // eslint-disable-next-line no-undef
            iconMarker = new google.maps.MarkerImage(
                require("../Assets/marker-pin-green.png"),
                null, /* size is determined at runtime */
                null, /* origin is 0,0 */
                null, /* anchor is bottom center of the scaled image */
                // eslint-disable-next-line no-undef
                new google.maps.Size(32, 32)
            );
        }
    },[])
    useEffect(()=>{
        GetToken({
          user_id:123,
          customer_id:456,
          iam_role:"encrypted_admin"
        }).then((data)=>{
          localStorage.setItem("jwt", JSON.stringify(data))
          console.log(data);
        }).catch(err => console.log(err))
      },[])
    useEffect(() => {
        if (!drawPreffred) {
            var res = getNearestServiceProvider(currentLocation, serviceProviders)
            res.then((destinationLatLon) => {
                drawRoute(currentLocation, destinationLatLon)
            })
        }
    }, [serviceProviders])
    const getServiceProviders = (e) => {
        e.preventDefault();
        setToggle(true)
        setToggleMap(false)
        setDrawPreffred(false);
        GetServiceProviders(currentLocation).then((data) => {
            console.log(data);
            setToggle(false);
            if (data.success) {
                if (data.data.status === "OK") {
                    if (data.preferred.length > 0) {
                        drawRoute(currentLocation, data.preferred[0].geometry.location)
                        setServiceProviders(data.data.results)
                        setDrawPreffred(true)
                    } else {
                        setServiceProviders(data.data.results)
                    }
                    setToggleMap(true)
                } else {
                    alert("No service providers arround you")
                }
            } else {
                alert("Something went wrong")
            }
        }).catch((err) => console.log(err))
    }
    const directionRef = useRef()
    const handleChange = (name, event) => {
        event.preventDefault();
        setClocation({ ...currentLocation, [name]: event.target.value })
    }
    function drawRoute(source, destination) {
        if (isLoaded) {
            setDirectionsResponse(null)
            // eslint-disable-next-line no-undef
            const directionsService = new google.maps.DirectionsService()
            directionsService.route({
                origin: { lat: Number(source.lat), lng: Number(source.lng) },
                destination: { lat: Number(destination?.lat), lng: Number(destination?.lng) },
                // eslint-disable-next-line no-undef
                travelMode: google.maps.TravelMode.DRIVING,
            },
                (result, status) => {
                    // eslint-disable-next-line no-undef
                    if (status === google.maps.DirectionsStatus.OK) {
                        //setDirectionsResponse(result)
                        if (directionRef.current) {
                            directionRef.current.setMap(null);
                        }
                        // eslint-disable-next-line no-undef
                        directionRef.current = new google.maps.Polyline({
                            path: result.routes[0].overview_path,
                            strokeColor: "#69A628",
                            strokeOpacity: 1.0,
                            strokeWeight: 5,
                        });
                        directionRef.current.setMap(map);
                    } else {
                        console.error(`error fetching directions ${result}`);
                    }
                })
        }
    }
    return (
        <div className="container p-5">
            <Modal
                open={toggle}
                onClose={() => setToggle(false)}
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <CircularProgress color="success" />
                    <h4 className="test-secondary">Fetching Service Providers</h4>
                </Box>
            </Modal>
            <div className='text-center p-3'>
                <h3 className="msg">Find Nearby Service Provider</h3>
                <TextField type={"number"} id="Lat" sx={{
                    width: 500,
                    maxWidth: '100%',
                }} label="Latitude" variant="outlined" onChange={e => handleChange("lat", e)} /><br /><br />
                <TextField type={"number"} id="Lng" sx={{
                    width: 500,
                    maxWidth: '100%',
                }} label="Longitute" variant="outlined" onChange={e => handleChange("lng", e)} /><br /><br />
                <TextField id="Pref" sx={{
                    width: 500,
                    maxWidth: '100%',
                }} label="Preffred Service Provider" variant="outlined" value={currentLocation.preferred} onChange={e => handleChange("preferred", e)} /><br /><br />
                <Button variant="contained" sx={{
                    width: 500,
                    maxWidth: '100%',
                }} onClick={e => { getServiceProviders(e) }} endIcon={<AddLocationIcon />}>Get Nearby Service Providers</Button>
            </div>
            <Box className="pt-3" display={toggleMap ? 'block' : 'none'}>
                {currentLocation !== undefined && isLoaded ?
                    <GoogleMap
                        center={{
                            lat: Number(currentLocation.lat),
                            lng: Number(currentLocation.lng)
                        }}
                        zoom={15}

                        mapContainerStyle={{ width: '100wh', height: '80vh' }}
                        onLoad={(map) => setTimeout(() => setMap(map))}
                    >
                        {map && (
                            <Marker title='You are here' position={{ lat: Number(currentLocation.lat), lng: Number(currentLocation.lng) }} />
                        )}
                        {map !== undefined && serviceProviders !== undefined ?
                            serviceProviders.map((serviceProvider, index) => {
                                if (isLoaded) {
                                    return (
                                        map && (
                                            <Marker icon={iconMarker} onClick={e => drawRoute({ lat: Number(currentLocation.lat), lng: Number(currentLocation.lng) }, { lat: Number(serviceProvider?.geometry?.location?.lat), lng: Number(serviceProvider?.geometry?.location?.lng) })} position={{ lat: Number(serviceProvider?.geometry?.location?.lat), lng: Number(serviceProvider?.geometry?.location?.lng) }} key={index}>
                                                {/* eslint-disable-next-line no-undef */}
                                                <InfoWindowF options={{ pixelOffset: new google.maps.Size(0, -10) }} position={{ lat: Number(serviceProvider?.geometry?.location?.lat), lng: Number(serviceProvider?.geometry?.location?.lng) }}>
                                                    <span>{serviceProvider?.name}</span>
                                                </InfoWindowF>
                                            </Marker>
                                        )
                                    )
                                }
                            })
                            : ''}
                        {directionsResponse && (
                            <DirectionsRenderer options={{
                                polylineOptions: {
                                    strokeColor: '#69A628'
                                },
                                markerOptions: {
                                    visible: false
                                },
                                hideRouteList: false
                            }} ref={directionRef} />
                        )}
                    </GoogleMap>
                    : ''}
            </Box>
        </div>
    );
}
export default GoogleMaps