import axios from "axios";


export const GetToken = (payload) =>{
    return (
        axios.post(`https://unqm0rx499.execute-api.ap-south-1.amazonaws.com/dev/geofencing/getToken`, payload, {
                
            }).then((res) => {
                return res.data;
            }).catch((err) => {
                console.log(err);
            })
    )
}

export const GetServiceProviders = (coordinates) => {
    return (
        axios.get(`https://unqm0rx499.execute-api.ap-south-1.amazonaws.com/dev/geofencing/service-provide?lat=${coordinates.lat}&lon=${coordinates.lng}&preferred=${coordinates.preferred}`, {
            // headers: {
            //     Authorization: localStorage.getItem("jwt")
            // }
        }).then((res) => {
                return res.data;
            }).catch((err) => {
                console.log(err);
            })
        )
}
